<template>
  <div>
    <div class="header">
      <v-header :seller="seller"></v-header>
    </div>
    <div class="tab border-1px">
      <div class="tab-item">
        <router-link to="/goods">商品</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/ratings">评论</router-link>
      </div>
      <div class="tab-item">
        <router-link to="/seller">商家</router-link>
      </div>
    </div>
    <keep-alive>
      <router-view :seller="seller"/>
    </keep-alive>
  </div>
</template>

<script>
  import vHeader from './components/header/header'

  export default {
    data () {
      return {
        seller: {
          id: (() => {
            let queryParam = this.$urlParse()
            return queryParam.id
          })()
        }
      }
    },
    components: {
      vHeader
    },
    created () {
      this.$axios.get('http://result.eolinker.com/Ma9FulJaecce6863833cfed8ad6d2a26c5bc224bfb7c159?uri=selldata&id=')
        .then((res) => {
          this.seller = Object.assign({}, this.seller, res.data.seller)
        })
    }
  }
</script>

<style lang="stylus" rel="stylesheet/stylus">
  @import "./common/stylus/mixin.styl"
  .tab
    display flex
    width 100%
    height 40px
    line-height 40px

    border-1px(color)

    .tab-item
      flex: 1
      text-align center

      & > a
        display block
        font-size 14px
        color #778593

        &.active
          color rgb(240, 20, 20);
</style>



